
#app{font-family:'Comfortaa', cursive;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;text-align:center;color:#2c3e50
}
#app>div>div{display:-ms-flexbox;display:-webkit-flex;display:-webkit-box;display:flex;-ms-flex-wrap:wrap;flex-wrap:wrap
}
#nav-menu a{height:calc((100vh - 3.778rem) / 3);display:-ms-flexbox;display:-webkit-flex;display:-webkit-box;display:flex;-ms-flex-pack:center;-webkit-box-pack:center;justify-content:center;-ms-flex-align:center;-webkit-box-align:center;align-items:center;width:100vw;color:#fff;font-weight:bold;font-size:2rem;text-decoration:none;-webkit-transition:all ease 0.4s;transition:all ease 0.4s
}
#nav-menu a:first-child{background-color:#0d47a1
}
#nav-menu a:nth-child(2){background-color:#1565c0
}
#nav-menu a:nth-child(3){background-color:#1976d2
}
#nav-menu a:nth-child(4){background-color:#19766f
}
#nav-menu a:hover:first-child{background-color:rgba(13,71,161,0.8)
}
#nav-menu a:hover:nth-child(2){background-color:rgba(21,101,192,0.8)
}
#nav-menu a:hover:nth-child(3){background-color:rgba(25,118,210,0.8)
}
#app.category #nav-menu a{width:calc(100vw / 3);height:80px;font-size:1.5rem
}
.mobile-bottom-bar{position:fixed;bottom:0;width:100%;height:3.778rem;background:#0d47a1;display:-ms-flexbox;display:-webkit-flex;display:-webkit-box;display:flex;-ms-flex-pack:center;-webkit-box-pack:center;justify-content:center;-ms-flex-align:center;-webkit-box-align:center;align-items:center
}
.mobile-bottom-bar .footer-link{-ms-flex:1 1 auto;-webkit-box-flex:1;flex:1 1 auto;text-align:center;color:#fff;text-transform:uppercase;font-weight:bold;padding:0.907rem 1rem
}
.mobile-bottom-bar .footer-link i.fa{opacity:0.8;vertical-align:middle
}
.mobile-bottom-bar .footer-link:active,.mobile-bottom-bar .footer-link:focus{color:#051d43
}
.mobile-bottom-bar .footer-text{position:relative;top:2px;font-weight:bold;font-size:rem-calc(14);color:#fff
}



